"use client";

import { Inter } from "next/font/google";
import Image from "next/image";
import Fly from "@/app/fly-box/page";
import { useRouter } from "next/navigation";

const Interfont = Inter({
  subsets: ["latin"],
  weight: "400",
});

export default function Box() {
  const router = useRouter();

  const handleClick = () => {
    router.push("/modal");
  };

  return (
    <>
      <div
        className={`${Interfont.className} flex flex-wrap md:flex-nowrap items-center justify-between bg-gradient-to-br from-blue-400 from-34% via-purple-600 via-50% to-fuchsia-900 to-100% p-6 md:p-10 min-h-[38rem]`}
      >
        {/* HERO */}
        <span className="text-3xl md:text-5xl font-semibold text-gray-900 md:w-1/2 w-full mb-6 md:mb-0 md:pl-5">
          <span>YINLINOKU</span>
          <br />
          <span className="text-purple-800"> Decentralized </span>
          <br /> Donation for people
          <br />
          <span className="text-xl md:text-2xl">
            donasikan hartamu menggunakan teknologi{" "}
            <span className="text-amber-900">Blockchain</span>
          </span>
          <br />
          <button
            onClick={handleClick}
            className="inline-block bg-red-600 hover:shadow-[0_0_10px_rgba(255,0,0,0.8)] text-white font-semibold py-2.5 sm:py-3.5 px-6 sm:px-10 rounded-[10px] cursor-pointer transition duration-300 text-sm sm:text-base mt-4"
          >
            Get Started
          </button>
        </span>

        {/* Gambar */}
        <div className="w-full md:w-auto flex justify-center mb-30 md:justify-end">
          <Image
            src="/gojo1.png"
            alt="Character"
            width={450}
            height={200}
            className="max-w-full h-auto pr-10"
          />
        </div>
      </div>
      {/* SECTION */}
      <div className=" flex items-center justify-between bg- p-8 w-screen  min-h-[38rem]">
        <div className="bg-amber-100 w-100 h-70 rounded-2xl"></div>
        <div className="bg-amber-100 w-100 h-60 rounded-2xl"></div>
        <div className="bg-amber-100 w-100 h-70 rounded-2xl"></div>
      </div>
    </>
  );
}
